<style>
  .cursor-pointer{
    cursor: pointer;
    color: #409EFF;
  }
  .new-price p{
    line-height: 26px;
  }
  .new-price p:nth-of-type(1){
    color: orange;
  }
  .new-price p:nth-of-type(3){
    font-weight: bold;
  }
  .adjust-price .el-button--mini{
    padding: 6px 5px;
  }
</style>
<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="configPrice" size="mini" icon="el-icon-setting">配置系统定价</el-button>
        <el-button type="primary" @click="getFailList" size="mini" icon="el-icon-view">获取失败查看</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="商品名称">
            <el-input class="search-input" v-model.trim="searchContent.productName" placeholder="请输入商品名称" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
          </el-form-item>
          <el-form-item label="ERP编码">
            <el-input class="search-input" v-model.trim="searchContent.erpNumber" placeholder="请输入ERP编码" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
          </el-form-item>
          <el-form-item label="审核状态">
            <el-select class="search-input" v-model="searchContent.checkStatus" filterable placeholder="请选择审核状态" size="small" @change="doSearchHandel">
              <el-option
                v-for="item in checkStatusList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="sp-handel-w">
        <el-button class="sup-mini" size="mini" :disabled="this.auditIdList.length === 0" @click="auditMore" icon="el-icon-circle-check-outline">批量审核</el-button>
      </div>
      <!-- 列表 -->
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        ref="multipleTable"
        @select="handleSelect"
        @select-all="handleSelectAll"
        style="width:100%;"
        size="small">
        <el-table-column fixed type="selection" label="全选" align="center"></el-table-column>
        <el-table-column min-width="80" prop="goodsNo" label="SKU编码"></el-table-column>
        <el-table-column min-width="80" prop="erpNumber" label="ERP编码"></el-table-column>
        <el-table-column min-width="180" prop="productName" label="商品名称"></el-table-column>
        <el-table-column min-width="90" prop="cityName" label="售卖区域"></el-table-column>
        <el-table-column min-width="100" prop="platformName" label="售卖平台"></el-table-column>
        <el-table-column min-width="80" prop="historyPrice" :formatter="nullTreating" label="历史售价"></el-table-column>
        <el-table-column min-width="80" prop="price" label="当前售价"></el-table-column>
        <el-table-column min-width="80" prop="newPrice" label="新售价">
          <template slot-scope="scope">
            <el-popover placement="bottom" width="310" trigger="hover">
              <div class="new-price">
                <p>新售价 = SKU进货价 /（1 - 平台毛利率）/ 平台折扣率</p>
                <p>{{scope.row.newPrice}} = {{scope.row.productCost}} /（1 - {{scope.row.profitRate}}）/ {{scope.row.discountRate}}</p>
                <p>参考信息</p>
                <p>历史进货价  {{scope.row.historyCost}}</p>
                <p>{{scope.row.productInfo}}</p>
              </div>
              <p class="cursor-pointer" slot="reference">{{scope.row.newPrice}}</p>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column min-width="160" prop="adjustPrice" label="手动调价">
          <template slot-scope="scope">
            <div v-if="scope.row.showUpdate == false && scope.row.checkStatus == 0" class="adjust-price">
              <el-button-group>
                <el-input v-model="scope.row.adjustPrice" size="mini" style="width: 80px;" @focus="modifyAdjustPrice(scope.$index, scope.row)"></el-input>
              </el-button-group>
            </div>
            <div v-else-if="scope.row.showUpdate == true && scope.row.checkStatus == 0" class="adjust-price">
              <el-button-group>
                <el-input v-model="scope.row.updateAdjustPrice" size="mini" style="width: 80px;"  @blur="deleteAdjustPrice(scope.$index, true)"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureAdjustPrice(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="deleteAdjustPrice(scope.$index)"></el-button>
              </el-button-group>
            </div>
            <div v-else>{{scope.row.adjustPrice}}</div>
          </template>
        </el-table-column>
        <el-table-column min-width="80" prop="checkStatus" :formatter="checkStatusTreating" label="审核状态"></el-table-column>
        <el-table-column min-width="170" prop="updateTime" label="变更时间"></el-table-column>
        <el-table-column min-width="170" prop="checkTime" :formatter="nullTreating" label="审核时间"></el-table-column>
        <el-table-column align="center" fixed="right" label="操作" width="110">
          <template slot-scope="scope">
            <el-button icon="el-icon-circle-check-outline" size="mini" v-if="scope.row.checkStatus == 0" @click="auditOne(scope.row)">审核</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[20, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!-- 确认审核列表 -->
    <el-dialog
      title="提示"
      :visible.sync="auditDialogVisible"
      width="30%">
      <span>确认审核已选择的商品吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="auditDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="sureAudit" size="mini">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 获取失败列表 -->
    <el-dialog title="获取区域进价成本失败的SKU" :visible.sync="isShowFailList" width="740px">
      <fail-list ref="FailList"></fail-list>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="isShowFailList = false" size="mini">关 闭</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import FailList from "./FailList"
import * as utils from '@/utils/utils'

const priceAuditUrl = {
  // 查询审核列表
  recordlist: '/plateform/a/pricing/recordlist',
  // 获取失败列表
  faillist: '/plateform/a/pricing/faillist',
  // 手动改价
  updateadjustprice: '/plateform/a/pricing/updateadjustprice',
  // 单个审核定价
  auditpricinglog: '/plateform/a/pricing/auditpricinglog',
  // 批量审核定价
  batchauditpricinglog: '/plateform/a/pricing/batchauditpricinglog'
}
export default {
  data() {
    return {
      loading: false,
      isShowFailList: false, // 显示失败列表
      auditDialogVisible: false, // 商品审核列表
      pageNum: 1,
      pageSize: 20,
      total: 0,
      tableData: [],
      platformList: [], // 平台列表
      checkStatusList: [
        {
          value: -1,
          label: '全部'
        },
        {
          value: 0,
          label: '待审核'
        },
        {
          value: 1,
          label: '已审核'
        }
      ],
      searchContent: { // 搜索
        productName: '', // 商品名称
        erpNumber: '', // ERP编码
        platformId: 3, // 平台ID（1：有赞，2：京东到家，3：美团外卖，4：饿百，不传或空表示所有平台）
        checkStatus: 0 // 审核状态 -1全部 0未审核 1 已审核
      },
      reg: /^[0-9]+\.[0-9]+$/,
      reg1: /^[0-9]*$/,
      auditSelectOne: '', // 审核一个
      SelectOne: true,
      auditIdList: [] // 批量审核ID
    }
  },
  created() {
  },
  mounted() {
    // 查询
    this.doSearchHandel(true)
  },
  components: {
    "fail-list": FailList
  },
  methods: {
    /**
     * 数据处理
     */
    nullTreating (row, column, cellValue) {
      if(cellValue == null) {
        cellValue = '-'
      }
      return cellValue
    },
    /**
     * [Treating 数据处理]
     * @return {[type]} [description]
     */
    checkStatusTreating (row, column, cellValue) {
      if(cellValue == 0) {
        cellValue = '待审核'
      }else if(cellValue == 1 || cellValue == 2) {
        cellValue = '已审核'
      }
      return cellValue
    },
    /**
     * [changeSize description]
     * @param    {[type]}                 value [description]
     * @return   {[type]}                       [description]
     */
    changeSize (value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 点击分页条]
     * @param    {[type]}                 value [当前页]
     * @return   {[type]}                       [description]
     */
    changeCurrent (value) {
      this.pageNum = value
      this.doSearchHandel(value)
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel(param) {
      if(param == null) {
        this.pageNum = 1
      }
      this.loading = true
      this.$http.post(priceAuditUrl.recordlist, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.result && res.data.result.length) {
            let tableData = res.data.result
            tableData.forEach((item, i) => {
              item['updateAdjustPrice'] = item['adjustPrice']
              item['showUpdate'] = false
            })
            this.tableData = tableData
            this.auditIdList = []
          }else {
            this.tableData = []
          }
          if(this.pageNum === 1) {
            this.total = res.data.total
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [handleSelect 选择某一个]
     * @return {[type]} [description]
     */
    handleSelect(val, row) {
      // 临时缓存使用
      let auditIdListTemp = [];

      if(utils.indexInArr(this.auditIdList, row.recordId) > -1) {
        this.auditIdList = utils.removeElFromArr(this.auditIdList, row.recordId)
        return
      }else {
        auditIdListTemp.push(row.recordId)
      }

      // 获取去重后的上传的参数
      this.auditIdListTemp = [...auditIdListTemp, ...this.auditIdList]
      this.auditIdList = Array.from(new Set(this.auditIdListTemp))

    },
    /**
     * [handleSelectAll 选择多个]
     * @return {[type]} [description]
     */
    handleSelectAll(val) {
      if(val.length > 0) {        
        // 全选
        for(let item of val) {
          this.auditIdList.push(item.recordId)
        }
        // 去重
        this.auditIdList = Array.from(new Set(this.auditIdList))

      }else { 
        // 全不选 此时val.length<=0
        // 1.获取当前table中每一条的id
        let auditIdList = []
        for(let i of this.tableData) {
          auditIdList.push(i.recordId)
        }
        // 2.从要传递的参数中去掉这些id
        this.auditIdList = Array.from(new Set([...this.auditIdList].filter(j => !new Set(auditIdList).has(j))))
      }
      
    },
    /**
     * [getFailList 获取失败查看]
     * @return {[type]} [description]
     */
    getFailList() {
      this.isShowFailList = true
      setTimeout(() => {
        // 调用子组件的方法
        this.$refs.FailList.setDefault()
      }, 200)
    },
    /**
     * [configPrice 配置系统定价]
     * @return {[type]} [description]
     */
    configPrice() {
      this.$router.push('/mt_price/config')
    },
    /**
     * [sureAdjustPrice 确认手动调价]
     * @return {[type]} [description]
     */
    sureAdjustPrice(val) {
      if(val.updateAdjustPrice == null || !(this.reg.test(val.updateAdjustPrice) == true || this.reg1.test(val.updateAdjustPrice) == true)) {
        this.$message("请输入正确的数字")
        return
      }
      this.loading = true
      this.$http.post(priceAuditUrl.updateadjustprice, {adjustPrice: val.updateAdjustPrice, logId: val.recordId}).then(res => {
        this.loading = false
        if(res.code === 0) {
          this.$message({
            message: '调价成功',
            type: 'success'
          })
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [deleteAdjustPrice 删除手动调价]
     * @return {[type]} [description]
     */
    deleteAdjustPrice(index, val) {
      if(val){
        setTimeout(() => {
          this.tableData[index].updateAdjustPrice = this.tableData[index].adjustPrice
          this.tableData[index].showUpdate = false
        }, 200)
      }else{
        this.tableData[index].updateAdjustPrice = this.tableData[index].adjustPrice
        this.tableData[index].showUpdate = false
      }
    },
    /**
     * [auditOne 单个审核]
     * @return {[type]} [description]
     */
    auditOne(val) {
      this.SelectOne = true
      this.auditDialogVisible = true
      this.auditSelectOne = val.recordId
    },
    /**
     * [auditMore 多个审核]
     * @return {[type]} [description]
     */
    auditMore() {
      this.SelectOne = false
      this.auditDialogVisible = true
    },   
    /**
     * [sureAudit 确认审核]
     * @return {[type]} [description]
     */
    sureAudit() {
      this.auditDialogVisible = false
      this.loading = true
      if(this.SelectOne == true) {
        this.$http.post(priceAuditUrl.auditpricinglog, {logId: this.auditSelectOne, status: 1}).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.$message({
              message: '审核成功',
              type: 'success'
            })
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }else{ // 批量审核
        this.$http.post(priceAuditUrl.batchauditpricinglog, {logIdList: this.auditIdList, status: 1}).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.$message({
              message: '批量审核成功',
              type: 'success'
            })
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    },   
    /**
     * [modifyAdjustPrice 输入]
     * @return {[type]} [description]
     */
    modifyAdjustPrice(index, val) {
      if(val.showUpdate == true) {
        return
      }
      this.tableData[index].showUpdate = true
    }
  }
}
</script>


