<template>
  <div>
    <!-- 操作框 -->
    <div>
      <el-form :model="searchContent">
        <el-form-item label="获取日期">
          <el-date-picker
            class="search-date-picker" 
            v-model="searchContent.getDate"
            type="date"
            placeholder="请选择日期"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            size="small"
            @change="doSearchHandel">
          </el-date-picker>
        </el-form-item>
      </el-form>
    </div>
    <!-- 搜索结果 -->
    <div>
      <!-- 列表 -->
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        height="280"
        style="width:100%;"
        size="small">
        <el-table-column min-width="100px" prop="getData" label="日期"></el-table-column>
        <el-table-column min-width="100px" prop="goodsNo" label="SKU编码"></el-table-column>
        <el-table-column min-width="100px" prop="erpNumber" label="ERP编码"></el-table-column>
        <el-table-column min-width="178px" prop="productName" label="SKU名称"></el-table-column>
        <el-table-column min-width="110px" prop="cityName" label="区域"></el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :pager-count="5"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
const priceAuditUrl = {
  // 获取失败列表
  faillist: '/plateform/a/pricing/faillist'
}
export default {
  data() {
    return {
      loading: false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      tableData: [],
      searchContent: {
        platformId: 3, // 平台ID（1：有赞，2：京东到家，3：美团外卖，4：饿百，不传或空表示所有平台）
        getDate: '' // 日期
      }
    }
  },
  methods: {      
    /**
     * [setDefault 设置默认]
     */
    setDefault(val) {
      this.doSearchHandel()
    }, 
    /**
     * [changeSize description]
     * @param    {[type]}                 value [description]
     * @return   {[type]}                       [description]
     */
    changeSize (value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 点击分页条]
     * @param    {[type]}                 value [当前页]
     * @return   {[type]}                       [description]
     */
    changeCurrent (value) {
      this.pageNum = value
      this.doSearchHandel()
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel() {
      this.loading = true
      this.$http.post(priceAuditUrl.faillist, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.result && res.data.result.length) {
            this.tableData = res.data.result
          }else {
            this.tableData = []
          }
          if(this.pageNum === 1) {
            this.total = res.data.total
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    }

  }

}
</script>


